:root {
    --swiper-navigation-size: 16px;
}
.allmovies{
    width: var(--inner);
    margin: auto;
    position: relative;
    padding: 44px 0;
}
.allmovies .top{
    margin-bottom: 8px;
}
.allmovies .top h2{
    font-size: 26px;
    font-weight: 600;
}
.allmovies .list{
    width: 150px;
    padding-bottom: 24px;
    cursor: pointer;
}
.allmovies .list:hover{
    background: var(--bg-col);
}
.allmovies .list .top{
    display: flex;
    justify-content: space-between;
}
.allmovies .list .imgbox{
    width: 100%;
    height: 225px;
}
.allmovies .list .imgbox img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
}
.allmovies .list .info{
    width: 100%;
}
.allmovies .list .info h3{
    font-size: 18px;
    font-weight: 500;
    /* 텍스트 1줄로 */
    white-space: nowrap;
    /* 넘친 텍스트 숨김 */
    overflow: hidden;
    /* 넘친 텍스트 말줄임표로 변경 */
    text-overflow: ellipsis;
}
.allmovies .list .info p{
    font-size: 14px;
    color: var(--txt-col3);
}
/* swiper 드래그바 */
.allmovies .swiper-scrollbar-drag{
    background: rgba(0,0,0,0.3);
}

/* swiper nav버튼 */
.controls{
    position: absolute;
    top: 60px;
    right: 10px;
    z-index: 10;
}
.swiper-button-prev,
.swiper-button-next{
    width: 28px;
    height: 28px;
    border-radius: 100%;
    border: 1px solid rgba(0,0,0,0.3);
    cursor: pointer;
}
.swiper-button-prev{
    position: absolute;
    left: -75px;
    top: 0;
}
.swiper-button-prev::after,
.swiper-button-next::after{
    display: block;
    font-size: var(--swiper-navigation-size);
    color: rgba(0,0,0,0.3);
}