.footer{
    width: 100%;
    background: var(--main-col);
    color: var(--white);
    padding: 44px 0 66px;
}
.footer .f_inner{
    max-width: var(--inner);
    min-width: 666px;
    margin: auto;
    display: flex;
    justify-content: center;
    gap: 40px;
}
.footer .f_inner .left{
    display: flex;
    flex-flow: column;
    gap: 44px;
}
.footer .f_inner .left img{
    width: 130px;
}
.footer .f_inner .left .join_btn{
    background: var(--white);
    padding: 8px 16px;
    border-radius: 4px;
    font-size: var(--body1-text);
    font-weight: 700;
    color: var(--second-col1);
}
.footer .f_inner .f_navwrap{
    display: flex;
    gap: 40px;
    font-size: var(--caption-text);
}
.footer .f_inner .f_navwrap .f_nav{
    display: flex;
    flex-flow: column;
    gap: 4px;
}
.footer .f_inner .f_navwrap .f_nav li{
    cursor: pointer;
}
.footer .f_inner .f_navwrap .f_nav li h4{
    font-size: var(--body1-text);
}