.header{
    width: 100%;
    background: var(--main-col);
    font-size: var(--head-text);
    font-weight: bold;
    color: var(--white);
}
.header .inner{
    max-width: var(--inner);
    min-width: 850px;
    height: 64px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    box-sizing: content-box;
}
.header .inner .left{
    display: flex;
    align-items: center;
    gap: var(--gap24);
}
.header .inner .left>.gnb{
    height: 100%;
    display: flex;
    gap: var(--gap32);
}
.header .inner .left h1 img{
    width: 154px;
    cursor: pointer;
}
.header .inner .primary{
    display: flex;
    align-items: center;
    gap: var(--gap32);
}
.header .inner .primary li{
    cursor: pointer;
}
.header .inner .primary .lang{
    border: 1px solid var(--white);
    background: var(--main-col);
    padding: 4px 4px;
}
.header .inner .primary .lang:hover{
    background: var(--white);
    color: var(--main-col);
}


.header .inner .left>.gnb>li{
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
}

/* 2차메뉴 */
.header .inner .left>.gnb>li>.depth2{
    width: 136px;
    background: var(--white);
    position: absolute;
    left: -5px;
    top: 55px;
    font-size: var(--body1-text);
    font-weight: 400;
    color: var(--txt-col1);
    border-radius: 4px;
    padding: 6px 0;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    z-index: 10;
    display: none;
}
.header .inner .left>.gnb>li:hover>.depth2 {
    display: block;
}
.header .inner .left>.gnb>li>.depth2 li{
    width: 100%;
    height: 30px;
    padding: 0 16px;
    display: flex;
    align-items: center;
}
.header .inner .left>.gnb>li>.depth2 li:hover{
    background: var(--txt-col5);
}

/* 검색창 */
.search_bar{
    width: 100%;
    height: 44px;
    border-bottom: 1px solid var(--bg-col);
}
.search_bar .search_inner{
    max-width: var(--inner);
    height: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 0 40px;
    box-sizing: content-box;
}
.search_bar .search_inner form{
    width: 100%;
    height: 100%;
}
.search_bar .search_inner form label{
    width: 100%;
    height: 100%;
}
.search_bar .search_inner form label input{
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font-size: var(--body2-text);
}