.banner{
    width: 100%;
    height: 360px;
    background-size : cover;
    background-position : center;
    position: relative;
}
.banner .b_inner{
    max-width: var(--inner);
    min-width: 850px;
    margin: auto;
    color: var(--white);
    display: flex;
    flex-flow: column;
    gap: 52px;
    padding: 0 40px;
    box-sizing: content-box;
}
.banner .b_inner .tit{
    padding-top: 90px;
}
.banner .b_inner .tit h3{
    font-size: 48px;
    line-height: 1;
}
.banner .b_inner .tit p{
    font-size: 32px;
    line-height: 0.9;
}
.banner .b_inner .search{
    width: 100%;
}
.banner .b_inner .search form{
    position: relative;
}
.banner .b_inner .search form label .s_btn{
    width: 82px;
    height: 48px;
    background: var(--second-col2);
    border-radius: 999px;
    border: none;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
    color: var(--white);
    font-size: var(--txt-col3);
    cursor: pointer;
}
.banner .b_inner .search form label .s_area{
    width: 100%;
    height: 48px;
    border-radius: 999px;
    text-indent: 20px;
    border: none;
    outline: none;
    font-size: var(--head-text);
}
.banner .b_inner .search form label input::placeholder{
    color: var(--txt-col3);
}
